import React, { useState } from 'react';
import { Route, Link } from 'react-router-dom';

export const Tabs = ({ tabs, match }) => {
  const [tabIndex, setIndex] = useState(0);

  return (
    <div className="tabs">
      {tabs.map(tab => (
        <Route
          path={`${match.path}/${tab.id}`}
          render={() => (
            <Tab tabs={tabs} tabIndex={tabIndex} />
          )}
        />
      ))}
      <div>
        {tabs.map((tab, index) => (
          <button
            key={tab.title}
            type="button"
            // className={classNames({
            //   tabs__button: true,
            //   selected: tabIndex === tab.index,
            // })}
            onClick={() => setIndex(index)}
          >
            <Link to={`${match.path}/${tab.id}`}>{tab.title}</Link>
          </button>
        ))}
      </div>
    </div>
  );
};

const Tab = ({ tabs, tabIndex }) => (
  <p className="tabs__content">
    {tabs[tabIndex].content}
  </p>
);
